.badge {
  display: inline-block;
  padding: 0.5em 1em;
  font-size: 100%;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.25rem;
  color: #ffffff;
}
.pill {
  padding-right: 0.9em;
  padding-left: 0.9em;
  border-radius: 10rem;
}
