.menus {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 16px;
  grid-row-gap: 16px;
}

.menus a {
  text-decoration: none;
  color: black;
}

.menus .menu {
  text-align: center;
  padding: 10px;
}
.menus .menu p {
  font-size: 20px;
}
