.footer {
  position: absolute;
  bottom: 0;
  background-color: #64d8cb;
  padding: 10px;
  width: 99.9%;
  height: 70px;
  left: -0;
  right: -0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 12px;
  grid-row-gap: 0px;
}

.footer div {
  text-align: center;
}
